import axios from "./axios";
import { _action, _loadNotifError } from "./action";

type AbcParam = {
  source: string;
}

const BASE_URL = `${process.env.VUE_APP_API_URL}/abc`

let abc = async ({ source } : AbcParam) => {
  let url = `${BASE_URL}/${source}`;
  const response = await axios.get(url);
  return response.data;
};

abc = _action("abc", "run the abc algorithm", abc);

export default abc;