<template>
  <div id="main-app">
    <v-navigation-drawer app width="300">
      <v-layout column align-center justify-center class="py-3 deep-purple accent-4">
        <v-avatar color="deep-purple accent-2" size="56">
          <span class="white--text headline">PK</span>
        </v-avatar>
        <div class="white--text title text-xs-center">Peta Kolhua</div>
      </v-layout>
      <v-card flat class="mt-2">
        <v-card-title>Rute</v-card-title>
        <v-card-text>
          <v-select
            dense
            hide-details
            label="Titik Awal"
            :items="optionsNodes"
            v-model="source"
          />
          <v-btn
            @click="runAbc"
            :disabled="source == undefined"
            outlined
            dark
            color="blue"
            class="mt-4">Jalankan</v-btn>
        </v-card-text>
      </v-card>
      <v-card flat class="mt-2">
        <v-card-title>Dijkstra</v-card-title>
        <v-card-text>
          <v-select
            dense
            hide-details
            label="Sumber"
            :items="optionsNodes"
            v-model="dijkstra.source"
          />
          <v-select
            dense
            hide-details
            label="Tujuan"
            :items="optionsNodes"
            v-model="dijkstra.target"
          />
          <v-btn
            @click="runDijkstra"
            :disabled="!dijkstraValid"
            outlined
            dark
            color="blue"
            class="mt-4">Jalankan</v-btn>
        </v-card-text>
      </v-card>
    </v-navigation-drawer>

    <div id="map-container" style="height: 100vh;">
      <div id="lmap-client" style="height: 100%;"></div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import { node as nodeRepo } from "@/services/repo";
import { CustomMap, edgeIsConnected } from "@/services/lmapd3";
import abc from "@/services/abc";
import dijkstra from "@/services/dijkstra"

export default Vue.extend({
  name: "MainApp",

  // Store current map reference.
  map: undefined,

  data: () => ({
    nodes: [],
    source: undefined,
    dijkstra: {
      source: undefined,
      target: undefined
    }
  }),

  methods: {
    async reload() {
      // Load all rt/rw
      const nodes = await nodeRepo.find({ jabatan: "all" });
      this.nodes = nodes;
      this.map.addNodes(nodes);
    },
    async runAbc() {
      this.map.removeLine();
      const source = this.source;
      if (source == undefined) {
        throw new Error("Source can't be empty");
      }
      let result = await abc({ source });
      // if (!edgeIsConnected(result)) {
      //   throw new Error(`edges is not connected`);
      // } else {
      //   console.log("yaaayy!! each edge connected properly");
      // }
      console.log(result[0]);
      console.log(result[1]);
      this.map.addEdgesAnimate(result);
    },
    async runDijkstra() {
      this.map.removeLine();
      if (!this.dijkstraValid) {
        return
      }

      const result = await dijkstra(this.dijkstra);
      this.map.addEdgesAnimate(result);
    }
  },
  computed: {
    optionsNodes() {
      return this.nodes.map(n => {
        return {
          value: n._id,
          text: `${n.jabatan.toUpperCase()}-${n.lokasiJabatan}`
        }
      })
    },
    dijkstraValid() {
      return (this.dijkstra.source != undefined) && (this.dijkstra.target != undefined)
    }
  },
  async mounted() {
    this.map = new CustomMap();
    this.map.init("lmap-client");
    await this.reload();
  }
});
</script>

