.elsy-lmap-icon {
  width: 40px;
  height: 40px;
  border-radius: 20px;

  box-shadow: #ddd 0 0 12px;
}
.elsy-lmap-icon.rt {
  background-color: #2196F3;
}
.elsy-lmap-icon.rw {
  background-color: #4CAF50;
}

