import axios from "./axios";

const BASE_URL = process.env.VUE_APP_API_URL;

async function dijkstra(payload : DijkstraParam){
  let url = `${BASE_URL}/dijkstra/${payload.source}/${payload.target}`;
  const response = await axios.get(url);
  return response.data;
}

type DijkstraParam = {
  source: string,
  target: string
};

export default dijkstra;